
.header {
    background-color: #4f5563; // Dark blue background
    padding: 0;
   width: auto;
}

.content {
    padding: 24px;
    min-height: 280px;
    background-color: #fff;
}

.footer {
    text-align: center;
    background-color: #001d58;
    color: #fff;
    padding: 16px;
}


.custom-header {
background-color: #003366 ; // Deep blue background
height: 100px; // Standard Ant Design header height
display: flex;
align-items: center;

.header-content {
    display: flex;
    margin-top: 10px;
    align-items: center;
    width: 100%;
    justify-content: center;
    .logo {
        img {
            height: 70px; // Adjust based on logo size
        }
    }

    .title {
        color: #ffcc00; // Yellow for the text
        font-size: 20px;
        font-weight: bold;
    }
}
}
.layout-default{
font-family: 'Times New Roman', Times, serif;
}
.navbar-header {
background-color: #003366 ; // Màu nền cho header
justify-content: left; // Căn giữa toàn bộ header
align-items: center; // Căn giữa theo chiều dọc
height: 30px; // Chiều cao của header
}

.navbar {
font-weight: bold;
display: flex;
font-size: 18px;
font-family: 'Times New Roman', Times, serif;
margin-top: -28px;
justify-content: center; // Căn giữa nội dung trong navbar
background-color: #003366 ; // Đảm bảo màu nền đen
}

.navbar .ant-menu-item a {
color: rgb(143, 24, 24); // Đặt màu trắng cho chữ
&:hover {
    color: #ffcc00; // Đổi màu khi hover
}
&.active {
    color: #1890ff; // Màu khi active
}
}
// Media Queries for responsiveness
@media (max-width: 768px) {
.header {
    .navbar {
        flex-direction: column; // Stack navbar items vertically on small screens
        align-items: center;
    }

    .header-container {
        flex-direction: column; // Stack logo and title vertically
        align-items: center;
    }

    .title {
        font-size: 18px; // Smaller title font size for mobile screens
    }
}
}


.ant-menu {
background-color: #003366  !important; // Màu nền cho menu
}

.ant-menu-item a {
color: white !important; // Màu chữ trắng cho menu item
}

.ant-menu-item:hover a {
color: #ffcc00 !important; // Màu chữ khi hover
}

.ant-menu-item-active a {
color: #003366 !important; // Màu chữ khi active
}/* Make the footer stick to the bottom */
html, body {
height: 100%;
margin: 0;
}

#root {
display: flex;
flex-direction: column;
min-height: 100%;
}

.footer {
font-size: 17px;
font-family: 'Times New Roman', Times, serif;
background-color: #003366 ; /* Dark blue background */
color: white;
text-align: center;
padding: 20px 0;
margin-top: auto; /* Ensures the footer sticks to the bottom */
}

.footer-menu {
list-style-type: none;
padding: 0;
margin: 0 0 15px;
}

.footer-menu li {
display: inline;
margin-right: 20px;
}

.footer-menu a {
color: white;
text-decoration: none;
font-weight: bold;
}

.footer-info p {
margin: 5px 0;
}

.footer-info {
font-size: 14px;
}
